import BigNumber from 'bignumber.js'
import {useCallback, useEffect, useRef, useState} from 'react';
import useSushi from "../hooks/useSushi";
import {useWallet} from "use-wallet";
import {balanceOf} from "./erc20";
import useBlock from "../hooks/useBlock";

export {default as formatAddress} from './formatAddress'

export const bnToDec = (bn: BigNumber, decimals = 18): number => {
    return bn.dividedBy(new BigNumber(10).pow(decimals)).toNumber()
}

export const decToBn = (dec: number, decimals = 18) => {
    return new BigNumber(dec).multipliedBy(new BigNumber(10).pow(decimals))
}

export const getAvatarUrl = (card: any) => {
    return `https://legend-shot.github.io/avatar/avatar_${card.quality}_${card.heroTypeId}_${card.prop}.png`
}


export const generateLink = (i18n: any, link: string) => {
    let baseUrl = `/${i18n.language}`;
    if (link === '/') {
        return `${baseUrl}`;
    }

    if (baseUrl === "/") {
        baseUrl = "";
    }

    return link.startsWith('/') ? `${baseUrl}${link}` : `${baseUrl}/${link}`;
}

export function useFetchState(props?: any) {
    const focus: any = useRef();
    const [state, setState] = useState(props);
    useEffect(() => {
        focus.current = true;
    }, []);
    const setFetchState = useCallback((params) => {
        focus.current && setState(params);
        return () => (focus.current = false);
    }, []);
    return [state, setFetchState];
}

export function useLgdBalance() {
    const sushi: any = useSushi()
    const {account} = useWallet()
    const block = useBlock()
    const [lgdBalance, setLgdBalance] = useFetchState()
    const [bigLgdBalance, setBigLgdBalance] = useFetchState(new BigNumber(0))
    useEffect(() => {
        if (sushi && account) {
            balanceOf(sushi.contracts.lgdToken, account).then(res => {
                setLgdBalance(sushi.web3.utils.fromWei(res))
                setBigLgdBalance(new BigNumber(res))
            })
        }
    }, [sushi, account, block, setBigLgdBalance, setLgdBalance])

    return {
        lgdBalance,
        bigLgdBalance
    }
}